import React from 'react'
import styled from '@emotion/styled';

import Box from 'components/Box';
import Flex from 'components/Flex';
import Link from 'components/Link';
import { responsive } from '../components/ThemeProvider/theme';
import { StaticImage } from 'gatsby-plugin-image';

const StyledHeader = styled(Flex)`
&::before {
  transition: 500ms opacity;
}
`

const Header = ({ onOpen, headerColor, ...props }) => {
  return (
    <StyledHeader
      as="header"
      position="absolute"
      bg="transparent"
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent={responsive('center', 'flex-start')}
      zIndex="docked"
      overflow="hidden"
      mx="5em"
      mt={responsive('0.5em', '3.5em')}
      {...props}
    >
      <Box.Relative width={responsive('5.625em', '8.625em')}>
        <Link to="/">
          {headerColor === 'white' ? (
            <StaticImage layout="fullWidth" src="white_logo.png" />
          ) : (
            <StaticImage layout="fullWidth" src="teal_logo.png" />
          )}
        </Link>
      </Box.Relative>
    </StyledHeader>
  )
}

export default Header
